@import '../../variables.scss';

.search-room {
  &__main {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;

    padding-top: 32px;

    margin-left: auto;
    margin-right: auto;
  }

  &__left-bar {
    width: 305px;
    flex-grow: 1;
    flex-shrink: 0;

    padding-right: 20px;
    padding-left: 20px;
  }

  &__content {
    flex-grow: 1;

    padding-left: 40px;
    padding-right: 20px;
    margin-bottom: 75px;
  }

  &__content-title {
    font-family: $headerFont;
    line-height: 30px;
    font-size: 24px;

    margin-top: 0;
  }

  &__content-items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__content-item {
    max-width: 32%;

    margin-bottom: 20px;
  }

  &__content-item-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__content-item-link:active {
    color: inherit;
  }

  &__content-pagination {
    margin-top: 22px;
    padding-right: 14px;
  }
}

@media (max-width: 1200px) {
  .search-room {
    &__content-item {
      max-width: 49%;
    }
  }
}

@media (max-width: 950px) {
  .search-room__main {
    flex-direction: column;
    align-items: center;
  }

  .search-room__content {
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 20px;
  }

  .search-room__left-bar {
    width: auto;
  }

  .search-room__content-title {
    text-align: center;
  }
}

@media (max-width: 610px) {
  .search-room__content-item {
    max-width: 100%;
  }
}
